.findwayCardWrapper{
    @apply flex flex-col justify-between h-3/4 items-center w-1/3 mt-12 p-3;
    
}

.findwayCardImage{
    @apply flex flex-row;
}

.findwayCardImageIcon{
    @apply text-6xl text-primary-teal;
}

.findwayCardImageText{
    @apply h-8 w-10 bg-primary-green text-center rounded-xl text-white;
}

.findwayCardTitle{
    @apply text-xl font-bold text-center;
}

.findwayCardBody{
    @apply text-center;
}

.findwayCardText{
    @apply flex flex-col justify-between ;
}