

.cardBox {
    @apply w-[23rem] my-4 shadow-xl rounded-bl-3xl rounded-br-3xl
}

.upSection {
    @apply bg-[url('../../resources/images/road.jpeg')] bg-cover bg-center rounded-tl-3xl rounded-tr-3xl py-6 px-6 h-[10.5rem]
}

.downSection {
    @apply px-6 w-full pb-4
}

.upSectionBox {
    @apply px-[10px] py-[4.5px] bg-gray-300 rounded-2xl max-w-fit opacity-90
}

.logoSection {
    @apply text-white flex items-center justify-center w-14 h-14 rounded-full shadow-xl bg-white -mt-8
}

.headText {
    @apply text-sm
}

.title {
    @apply text-black font-semibold
}

.description {
    @apply text-gray-500 mt-1
}

.bottomText {
    @apply text-sm
}
.circleLogo{
    @apply bg-blue-800 flex items-center justify-center w-8 h-8 rounded-full
}

.circle{
    @apply bg-gray-300 flex items-center justify-center w-8 h-8 rounded-full
}
