.partnerBody {
    @apply w-full;
    @apply mx-auto my-0 p-2;
    @apply lg:h-screen lg:w-11/12;
    @apply lg:mx-auto lg:px-5 lg:mb-20 lg:px-12;
}

.heading {
    @apply mx-auto my-4 w-max py-10;
    @apply text-2xl font-black;
    @apply md:text-4xl;
    @apply lg:mx-auto lg:my-8 lg:w-max;
    @apply lg:text-4xl;
}

/* .partnerLogoDiv {
    @apply bg-black;
} */
.partnerLogo {
    @apply mx-auto my-0 h-20;
    @apply  scale-75;
    @apply md:scale-90;
    @apply md:h-2/4;
    @apply lg:h-16;
    @apply md:scale-95 hover:scale-105 ease-out duration-500;
}
.partnerLogoEmbodiment {
    @apply grid grid-cols-2 gap-3;
    @apply w-full h-full;
    @apply mx-auto my-5;
    @apply md:grid-cols-4 md:gap-4 md:p-2 md:gap-2 md:w-full md:pb-20;
    @apply lg:grid lg:gap-2 lg:grid-cols-6 lg:p-5 lg:w-9/12 lg:h-full;
}
